.single-country {
    margin-left: 7.5%;
}

.back {
    margin: auto;
    max-width: 1300px;
    margin-bottom: 70px;
    display: flex;
    background: none !important;
}

.back button {
    height: 35px;
    width: 70px;
    background-color: white;
    border: 1px black solid;
    border-radius: 7px;
    transition: 0.3s;
}

.back button:hover {
    box-shadow: 0 0 7px black;
    cursor: pointer;
}

.App {
    margin-top: 4%;
}

.dark button {
    background-color: black;
    color: white;
}